import React from "react";
import './App.css';
import 'components/style.css';
import Footer from "components/Footer";
import Header from "components/Header";
import Aboutus from "components/sections/Aboutus";
import Contact from "components/sections/Contact";
import Main from "components/sections/Main";
import Ourwork from "components/sections/Ourwork";
import Partner from "components/sections/Partner";


//throttle :        
//(  scroll  )
const throttle = function (callback, waitTime) {
    let timerId = null;
    return (e) => {
        if (timerId) return;
        timerId = setTimeout(() => {
            callback.call(this, e);
            timerId = null;
        }, waitTime);
    };
};


function App() {
    // ,top   
    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        })
    }

    const sections = ['ourwork','aboutus','partner','contact'];

    return( 
        <div className="App">
            <Header scrollToTop={scrollToTop} throttle={throttle}/>
            <div id="wrap">
                <div className="container">          
                    <Main /> 
                    <Ourwork sections={sections}/>
                    <Aboutus sections={sections} throttle={throttle}/>
                    <Partner sections={sections}/>
                    <Contact sections={sections}/>
                </div>
            <Footer scrollToTop={scrollToTop}/>
            </div> 
        </div>
    )
}

export default App;