import React, { useEffect, useRef, useState } from "react";


const Aboutus = ({sections, throttle}) => {
    const [pageY,setPageY] = useState(0);
    const [motion, setMotion] = useState(false);
    const documentRef = useRef(document);

    const motionScroll = () => {
        const { pageYOffset } = window;
        const deltaY = pageYOffset - pageY; //   -  
        if(pageYOffset > 1500 && pageYOffset < 2800) {
            if(deltaY>=0) {
                //console.log(' ');
                setMotion(true); //   true motion_down  
            }
            else {
                //console.log(' ');
                setMotion(false);//   false motion_up z 
            }
        }
        setPageY(pageYOffset); //   scroll 
    }

  
 
    
    const throttleScroll = throttle(motionScroll, 50);

    useEffect(() => {
        documentRef.current.addEventListener('scroll', throttleScroll);
        return () => documentRef.current.removeEventListener('scroll', throttleScroll);
    }, [pageY]);


    return (
        <div id={sections[1]} className="element" name="section2">
            <div className="cont_wrap">
                <div className="textBox">
                    <div className="title">
                        <h1>ABOUT US</h1>
                        <p>
                              
                            <br />
                            ~
                            <br />
                            ~
                        </p>
                    </div>
                    <p>
                              
                         
                        <br />
                             !
                        <br />
                               
                        .
                        <br />
                           
                        <br />
                           .
                    </p>
                    <div className={"light " + (motion ? 'motion_down' : 'motion_up')}>
                        <img src="images/light.png" alt="" />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Aboutus;
